<template>
    <p-data-table
        :value="reportList"
        v-model:selection="selectedReport"
        selectionMode="single"
        dataKey="autoTestOrderId"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :loading="reportLoading"
        :paginator="false"
        :rows="20"
        paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
        currentPageReportTemplate="Showing {first} to {last} of {totalRecords} entries"
        :rowsPerPageOptions="[10, 20, 50]"
    >
        <p-column header="" field="checked">
            <template #body="{data}">
                <p-checkbox
                    v-model="data.checked"
                    :binary="true"
                    @change="checkedOneOnChange(data)"
                />
            </template>
        </p-column>
        <p-column header="#" field="testOrderId" />
        <p-column header="Last Name" field="lastName" />
        <p-column header="First Name" field="firstName" />
        <p-column header="Birth Date" field="birthDate" />
        <p-column header="Cycle" field="cycleType" />
        <p-column header="Date" field="orderDate" />
        <p-column header="E2" field="etwo" />
        <p-column header="LH" field="lh" />
        <p-column header="PROG" field="prog" />
        <p-column header="FSH" field="fsh" />
        <p-column header="TSH" field="tsh" />
        <p-column header="PRL" field="prl" />
        <p-column header="BhCG" field="bhcg" />
        <p-column header="DHEAS" field="dheas" />
        <p-column header="AMHP" field="amhp" />
        <p-column header="!!" field="checked2">
            <template #body="{data}">
                <p-checkbox
                    v-model="data.checked2"
                    :binary="true"
                    @change="checkedTowOnChange(data)"
                />
            </template>
        </p-column>
        <p-column header="Doctor" field="doctorName" :sortable="true" />
        <p-column header="Loc" field="location" :sortable="true" />
        <p-column header="Modified" field="lastModifiedUser" />
        <p-column header="Modified !!" field="lastModifiedUser2" />

        <template #footer>
            <p-button
                label="Assign Cycle"
                class="p-mr-2"
                @click="assignCycle"
            />

            <p-button label="Encounter" class="p-mr-2" />

            <p-button
                label="View Blood Billing"
                class="p-mr-2"
                @click="showBloodBillingDialog"
            />

            <p-button
                label="View Funded Billing"
                class="p-mr-2"
                @click="showFundedBillingDialog"
            />

            <p-button
                label="View Cycle Monitoring"
                class="p-mr-2"
                @click="viewCycleMonitoring"
                :loading="cycleMonitoringBtnLoading"
            />
            <p-button label="PDF Blood Report" class="p-mr-2" />
        </template>
    </p-data-table>
</template>

<script>
import { useDailyReportReportsInit } from "@/composable/daily-report";
import { emitter } from "@/event-bus";
export default {
    setup() {
        const {
            reportList,
            selectedReport,
            reportLoading,
            checkedOneOnChange,
            checkedTowOnChange,
            cycleMonitoringBtnLoading,
            fundedBillingDialog,
            bloodBillingDialog
        } = useDailyReportReportsInit();

        return {
            reportList,
            reportLoading,
            selectedReport,
            checkedOneOnChange,
            checkedTowOnChange,
            cycleMonitoringBtnLoading,
            fundedBillingDialog,
            showFundedBillingDialog: () => {
                fundedBillingDialog.value = true;
            },
            showBloodBillingDialog: () => {
                bloodBillingDialog.value = true;
            },
            assignCycle: () => {
                emitter.emit("daily-report-report:assign-cycle");
            },
            viewCycleMonitoring: () => {
                emitter.emit("daily-report-report:view-cycle-monitoring");
            }
        };
    }
};
</script>
